import React, { Component } from 'react';
import { Router, Route, IndexRoute, browserHistory } from 'react-router'; // hashHistory || browserHistory

// Page's Layout
import Layout from './templates/Layout';

// Pages
import Home from './pages/Home';
import Catalog from './pages/Catalog';
import Product from './pages/Product';

class App extends Component {
  render() {
    return(
      <Router history={browserHistory}>
        <Route path="/" component={Layout}>
          <IndexRoute component={Home} />
        </Route>

        <Route path="/catalog" component={Layout}>
          <IndexRoute component={Catalog} />
          <Route path=":id" component={Catalog} />
        </Route>
        
        <Route path="/products" component={Layout}>
          <Route path=":id" component={Product} />
        </Route>

        <Route path="/auth" component={Layout}>
          <Route path="login" component={Home} />
          <Route path="register" component={Home} />
        </Route>
      </Router>
    );
  }
}

export default App;


