import React, { Component } from 'react';
import { Link } from 'react-router';
import { bind } from 'decko';

import logo from '../images/logo.png';

class Header extends Component {
  state = {
    showCatalog: false,
    categories: [],
    loading: true
  }

  componentWillMount() {
    fetch('/data/categories.json')
      .then(res => res.json())
      .then(data => {
        this.setState({
          categories: data.response,
          loading: false
        });
      })
      .catch(err => console.error(err));
  }

  @bind 
  toggleCatalog() {
    this.setState({ showCatalog: !this.state.showCatalog });
  }

  render() {
    const { showCatalog, categories } = this.state;

    console.log(categories);

    return(
      <header className="header">
        <div className="header__top">
          <div className="container">
            <div className="header-controls-inner pull-right">
              <ul className="header-controls">
                <li className="header-controls__item">
                  <a className="header-controls__link" href="/auth/login">Вход</a></li>
                <li className="header-controls__item">
                  <span className="header-controls__separator">|</span></li>
                <li className="header-controls__item">
                  <a className="header-controls__link" href="/auth/register">Регистрация</a></li>
              </ul>
            </div>
          </div>
        </div>
        <div className="header__bottom">
          <nav className="navbar navbar-default">
            <div className="container">
              <div className="navbar-header">
                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navigation" aria-expanded="false">
                  <span className="sr-only">Toggle menu</span>
                  <span className="icon-bar"></span>
                  <span className="icon-bar"></span>
                  <span className="icon-bar"></span>
                </button>
                <Link className="navbar-brand" to="/">
                  <img src={logo} alt="Vabol" />
                </Link>
              </div>
              
              <div className="header-collapse collapse navbar-collapse" id="navigation">
                <ul className="header-catalog nav navbar-nav">
                  <li className={ 'dropdown' + (showCatalog ? ' open' : '') } onMouseOver={this.toggleCatalog} onMouseOut={this.toggleCatalog} >
                    <Link to="/catalog/" className="header-catalog__link dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
                      Каталог <span className="caret"></span>
                    </Link>
                    <ul className="dropdown-menu">
                      { categories.map( (category, index) => {
                        return <li key={index}><Link to={'/catalog/' + category._id}>{category.name}</Link></li>;
                      }) }
                    </ul>
                  </li>
                </ul>

                <form className="header-form navbar-form navbar-left">
                  <div className="header-form-inner">
                    <input type="text" className="header-form__input form-control" placeholder="Введите запрос" />
                    <button type="submit" className="header-form__submit btn btn-default">
                      <i className="fa fa-search" aria-hidden="true"></i>
                    </button>
                  </div>
                </form>

                <ul className="nav navbar-nav navbar-right">
                  <li>
                    <Link to="/cart" className="header-cart">
                      <i className="header-cart__icon fa fa-shopping-cart" aria-hidden="true"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </header>
    );
  }
}

export default Header;


