import React from 'react';

function filter() {
  return(
    <div className="filter">

      <div className="filter-group">
        <div className="filter-title">
          <strong>Цена</strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1" />
            <span className="filter-item__text" >До 100 руб.</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" >От 100 руб. до 500 руб.</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" >От 500 руб. до 1000 руб.</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r1"/>
            <span className="filter-item__text" >От 1000 руб.</span>
          </label>
        </div>
      </div>

      <hr/>
      
      <div className="filter-group">
        <div className="filter-title">
          <strong>Тип изделия</strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2" />
            <span className="filter-item__text" >Готовое изделие</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2"/>
            <span className="filter-item__text" >На заказ</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="radio" name="r2"/>
            <span className="filter-item__text" >На пример</span>
          </label>
        </div>
      </div>

      <hr/>

      <div className="filter-group">
        <div className="filter-title">
          <strong>Тип материала</strong>
        </div>
        <div>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Хлопок</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Дерево</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Кожа</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Бисер</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Шерсть</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Стекло</span>
          </label>
          <label className="filter-item">
            <input className="filter-item__radio" type="checkbox" />
            <span className="filter-item__text" >Ткань</span>
          </label>
        </div>
      </div>

    </div>
  );
}

export default filter;


