import React, { Component } from 'react';
import { Link } from 'react-router';

// images
import indexImg from '../images/product/index.jpg';
import img_0 from '../images/product/0.jpg';
import img_1 from '../images/product/1.jpg';

import avatar from '../images/product/avatar.jpg';

import demo_0 from '../images/product/list/0.jpg';
import demo_1 from '../images/product/list/1.jpg';
import demo_2 from '../images/product/list/2.jpg';
import demo_3 from '../images/product/list/3.jpg';

class Product extends Component {
  render() {
    return(
			<div className="page">

				{/*breadcrumb*/}
				<div className="container">
					<ol className="breadcrumb">
						<li><Link to="/">Главная</Link></li>
						<li><Link to="/catalog">Каталог</Link></li>
						<li className="active">
							<span>Черный кожаный мужской кошелек</span>
						</li>
					</ol>
				</div>

				{/*data product*/}
				<div className="container product">

					<div className="row">
						<div className="col-md-6">

							<div className="galery-product">
								<img className="img-responsive" src={indexImg} alt=""/>

								<div className="galery-product__list">
									<div className="galery-product__list-item">
										<img className="img-responsive" src={img_0} alt=""/>
									</div>
									<div className="galery-product__list-item">
										<img className="img-responsive" src={img_1} alt=""/>
									</div>
								</div>
							</div>

						</div>
						<div className="col-md-6">

							<h1 className="product-title">Черный кожаный мужской кошелек</h1>

							<div className="product-price">
								<span className="price">1240</span>
								<span className="currency">руб.</span>
							</div>

							<div className="product-status">
								<span className="status">В наличие</span>
							</div>

							<div className="row">
								<div className="col-xs-6">

									<form action="">
										<button className="button button-default product-btn">Купить</button>
									</form>

								</div>
								<div className="col-xs-6">

									<div className="product-ui">
										<i className="fa fa-heart-o fa-2x"></i>
										<span className="product-ui__text">Избранное</span>
									</div>

									<div className="product-ui">
										<i className="fa fa-comment-o fa-2x"></i>
										<span className="product-ui__text">Задать вопрос</span>
									</div>

								</div>
							</div>

							<hr/>

							<div className="row">
								<div className="col-xs-1">
									<i className="fa fa-umbrella fa-3x"></i>
								</div>
								<div className="col-xs-11">
									<span>Защищенные покупки<br/>на 1000 руб.</span>
								</div>
							</div>

							<hr/>

							<div className="seller">
								<div className="seller__header">
									<strong>Продавец:</strong>
								</div>

								<div className="row">
									<div className="col-xs-2">
										<Link className="temp-link">
											<img className="img-responsive img-circle" src={avatar} alt=""/>
										</Link>
									</div>
									<div className="col-xs-10">

										<Link className="temp-link"><strong className="seller-name">Moroz</strong></Link>

										<span className="seller-star" title="Рейтинг продавца">
											<i className="fa fa-star"></i>
											<span> (271)</span>
										</span>

										<div className="adress">
											<span>Россия, г. Хабаровск</span>
										</div>

										<div className="phone">
											<span>+7 (000) 000 00-00</span>
											<span> (МТС)</span>
										</div>

									</div>
								</div>
							</div>

						</div>
					</div>

				</div>

				{/* tabs */}
				<div className="container">
					<div className="tabs">
						<div className="tabs-ui">

							<div className="tab-ui active">
								<span>Описание</span>
							</div>
							<div className="tab-ui">
								<span>Способы доставки и оплаты</span>
							</div>
							<div className="tab-ui">
								<span>История продукта</span>
							</div>

						</div>

						<div className="tabs-content">

							<div className="tab-content active">
								<div className="tab-content__block">
									<p>
										<strong>Тип изделия:</strong>
										<span> Готовое изделие</span>
									</p>
									<p>
										<strong>Материал:</strong>
										<Link className="link temp-link" > Натуральная кожа</Link>
									</p>
								</div>
								<div className="tab-content__block">
									<h4>Краткое описание:</h4>
									<p>
										Самая популярная модель - портмоне в два сложения, которое можно носить в брючном кармане и в сумке; 
										успешно сочетается с любым стилем в одежде. Здесь можно хранить купюры, визитки, пластиковые карты, мелочь 
										и даже документы. Кошелек сделан из кожи высшего качества, закрывается на кнопку. 
									</p>

									<h4>Характеристики:</h4>
									<ul>
										<li>Материал: кожа; </li>
										<li>Цвет кожи: черный; </li>
										<li>Цвет нити: белая; </li>
										<li>Особенности модели: 3 отделения для карточек, 1 кармашек для мелочи, 1 маленький кармашек для документов, 
											1 отделение для купюр; </li>
										<li>Полностью сшито вручную; </li>
										<li>Размер изделия: 9*11 см. </li>
									</ul>

									<p>
										<strong>*</strong>
										<span> В изделие можно внести конструктивные и цветовые изменения.</span>
									</p>

									<p>
										<strong>**</strong>
										<span> Оттенок изделия в действительности может отличаться от изображений на экране из-за различных 
											технических характеристик и настроек монитора!</span>
									</p>

								</div>
							</div>
							<div className="tab-content">
								{/**/}
							</div>
							<div className="tab-content">
								{/**/}
							</div>

						</div>
					</div>
				</div>

				{/* products avtor*/}
				<div className="container">
					<h3>Другие изделия мастера</h3>
					<div className="row products-list__inner">
						<div className="product-item col-md-3">

							<Link className="product-item__link">
								<img className="product-item__image img-responsive" src={demo_0} alt="" />
								<div className="product-item__info">
									<div className="product-item__title text-left">
										<h5>Кожаный коричневый мужской кошелек</h5>
										<strong className="text-left">1200 руб.</strong>
									</div>
								</div>
							</Link>

						</div>
						<div className="product-item col-md-3">

							<Link className="product-item__link">
								<img className="product-item__image img-responsive" src={demo_1} alt="" />
								<div className="product-item__info">
									<div className="product-item__title text-left">
										<h5>Черный маленький кожаный муской кошелек</h5>
										<strong className="text-left">980 руб.</strong>
									</div>
								</div>
							</Link>

						</div>
						<div className="product-item col-md-3">

							<Link className="product-item__link">
								<img className="product-item__image img-responsive" src={demo_2} alt="" />
								<div className="product-item__info">
									<div className="product-item__title text-left">
										<h5>Красный маленький кожаный женский кошелек</h5>
										<strong className="text-left">1050 руб.</strong>
									</div>
								</div>
							</Link>

						</div>
						<div className="product-item col-md-3">

							<Link className="product-item__link">
								<img className="product-item__image img-responsive" src={demo_3} alt="" />
								<div className="product-item__info">
									<div className="product-item__title text-left">
										<h5>Черный кожаный мужской кошелек портмоне</h5>
										<strong className="text-left">1150 руб.</strong>
									</div>
								</div>
							</Link>

						</div>

					</div>
				</div>
			</div>
    );
  }
}

export default Product;


