class Catalog extends Component {
  state = {
    categories: [],
    products: [],
    loading: true
  }

  componentWillMount() {
    fetch('/data/categories.json')
      .then(res => res.json())
      .then(data => {
        this.setState({
          categories: data.response,
          loading: false
        });
      })
      .catch(err => console.error(err));

    fetch('/data/products.json')
      .then(res => res.json())
      .then(data => {
        this.setState({
          products: data.response,
          loading: false
        });
      })
      .catch(err => console.error(err));
  }

  render() {
    const { categories, products } = this.state;
    const col = [7, 14];

    // let count = categories.length;
    // let k = count % 3;
    // let col = count / 3;
    // let c;
    // let array = [];

    // if (k > 0) {
    //   c = Math.floor(col) + 1;
    //   let t = 0;

    //   for(let i = 0; i < 2; i++) {

    //     for(let j = t; j < c; j++) {
    //       if(!array[i]) {
    //         array[i] = [];
    //       }
    //       array[i].push(categories[t]);
    //       t++;
    //     }

    //   }

    //   let find = count - (c * 2);

    //   for(let j = t; j < count; j++) {
    //     if(!array[2]) {
    //       array[2] = [];
    //     }
    //     array[2].push(categories[j]);
    //   }

    //   console.log(array);
    // }

    return (
      <div className="catalog page">

        {/*breadcrumb*/}
        <div className="container">
          <ol className="row breadcrumb">
            <li><Link to="/">Главная</Link></li>
            <li className="active">
              <span>Каталог</span>
            </li>
          </ol>
        </div>

        {/* products */}
        <div className="container">
          <div className="row">
            <div className="col-md-4 col-lg-3">
              <Sidebar/>
            </div>
            <div className="col-md-8 col-lg-9">
              {/* category */}
              <div className="catalog__categories">
                <div className="row">
                  <div className="col-md-4">
                    <ul className="categories">
                      <li><a className="categories__link" href="#">Аксессуары</a> <small>(10)</small></li>
                      <li><a className="categories__link" href="#">Сумка</a> <small>(6)</small></li>
                      <li><a className="categories__link" href="#">Украшения</a> <small>(18)</small></li>
                    </ul>
                  </div>
                  <div className="col-md-4">
                    <ul className="categories">
                      <li><a className="categories__link" href="#">Косметика</a> <small>(10)</small></li>
                      <li><a className="categories__link" href="#">Товары для дома</a> <small>(6)</small></li>
                      <li><a className="categories__link" href="#">Товары для детей</a> <small>(18)</small></li>
                    </ul>
                  </div>
                  <div className="col-md-4">
                    <ul className="categories">
                      <li><a className="categories__link" href="#">Игрушки и куклы</a> <small>(10)</small></li>
                      <li><a className="categories__link" href="#">Женская одежда</a> <small>(6)</small></li>
                    </ul>
                  </div>
                </div>
              </div>

              <ProductList data={products} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Catalog;


