const initialState = {
  isLogined: false,
  authModal: false
};

export default function auth(state = initialState, action) {
  switch (action.type) {
  case 'USER_LOGIN':
    return {
      ...state,
      isLogined: true,
      authModal: false
    };
  case 'USER_LOGOUT':
    return {
      ...state,
      isLogined: false
    };
  case 'USER_REGISTER':
    return state;
  case 'AUTH_HIDE':
    return {
      ...state,
      authModal: false
    };
  case 'AUTH_SHOW':
    return {
      ...state,
      authModal: true
    };
  default:
    return state;
  }
}


