import React from 'react';
import { Link } from 'react-router';

function productList({data}) {
  return(
    <div className="row products-list__inner">

      { data.map( (item, index) => {
        return(
          <div key={index} className="product-item col-md-3">

            <Link to={'/products/' + item._id} className="product-item__link">
              <img className="product-item__image img-responsive" src={'/uploads/product/' + item.thumb} alt={item.name} />
              <div className="product-item__info text-left">
                <div className="product-item__title text-left">
                  <h5>{item.name}</h5>
                  <strong className="text-left">{item.price}</strong>
                </div>
              </div>
            </Link>

          </div>
        );
      }) }

    </div>
  );
}

export default productList;


